//MEDIA QUERIES
$S: 400px;
$M: 640px;
$L: 960px;
$XL: 1200px;
@import 'mixins';
html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body,
html {
    min-height: 100vh;
    min-width: 100vw;
    border: none;
    padding: 0px;
    margin: 0px;
    display: block;
    font-family: 'Avenir Next', 'Avenir', 'helvetica neue', arial, sans-serif;
    color: #333333;
    overflow-x:hidden;
}

body {
    background-image: url(/pentagon.png);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 600;
}

h3 {
    line-height:1.1;
    margin-bottom:4px;
    font-size:1.1em;
}

// h3 + h4 {
//     margin-bottom:0px;
//     line-height:1;
// }

h4 {
    margin-top:0px;
    color:lighten(#333333,20%);
    line-height:1.2;
    font-size:1em;
    font-weight:normal;
}

a {
    color: #34495E;
    text-decoration: underline;
    // font-weight:bolder;
    &:hover {
        text-decoration: underline;
    }
    &.icon-link {
        text-decoration: none;
        &:hover {
            text-decoration: none;
        }
    }
}

main.main {
    display: block;
    position: relative;
    width: 95%;
    padding: 1em;
    background-color: #fff;
    margin-left: auto;
    margin-right: auto;
    max-width: 800px;
    padding-bottom:60px;
    header {
        h1.page-title {
            border-bottom: 2px solid #333333;
            line-height: 1;
            margin-bottom: 8px;
            margin-top: 1.2em;
            font-size: 28px;
            @include MQ(M) {
                font-size: 36px;
            }
        }
        img.headshot {
            width: 80px;
            top: 2em;
            @include MQ(M) {
                width: 140px;
                top: 1em;
            }
            filter: grayscale(100%);
            border-radius: 50%;
            position: absolute;
            right: .5em;
        }
    }
}

table {
    margin: 1em 0;
    min-width: 300px;
    width: 100%;
    th {
        width: 33.3%;
    }
}

footer {
    text-align:center;
    font-size:.9em;
    margin-top:2em;
    border-top:1px solid #333333;
    padding-top:1em;
    small {
        font-size:1em;
    }
}

table tr {
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
}

// table th {
//     display: none;
// }
table td {
    display: block;
}

svg#email-icon:hover {
    fill: #0077B5;
}

svg#linkedin-icon:hover {
    fill:#0077B5;
}

table td:first-child {
    padding-top: .25em;
}

table td:last-child {
    padding-bottom: .25em;
}

address {
    font-style:normal;
}

.social-links {
    display:block;
    margin-top:.5em;
}

.hobby-images {
    display:block;
    margin-left:auto;
    margin-right:auto;
    width:400px;
    img {
        max-width:195px;
    }
}

