table th,
table td {
    text-align: left;
}

@media (min-width: 480px) {
    table th,
    table td {
        display: table-cell;
        padding: .15em .5em;
    }
    table th:first-child,
    table td:first-child {
        padding-left: 0;
    }
    table th:last-child,
    table td:last-child {
        padding-right: 0;
    }
}

table {
    background: #34495E;
    color: #fff;
    // border-radius: .4em;
    overflow: hidden;
}

table tr {
    border-color: #46637f;
}

table th,
table td {
    margin: .25em 1em;
}

@media (min-width: 480px) {
    table th,
    table td {
        padding: .25em !important;
    }
}

thead {
    padding: 0px;
}

thead > tr {
    border-bottom: 2px solid white;
    border-color: white;
    padding: 0px;
    th {
        // border: 2px solid white;
        padding: 0px;
    }
}
