html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
html {
  min-height: 100vh;
  min-width: 100vw;
  border: none;
  padding: 0px;
  margin: 0px;
  display: block;
  font-family: 'Avenir Next', 'Avenir', 'helvetica neue', arial, sans-serif;
  color: #333333;
  overflow-x: hidden;
}

body {
  background-image: url(/pentagon.png);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

h3 {
  line-height: 1.1;
  margin-bottom: 4px;
  font-size: 1.1em;
}

h4 {
  margin-top: 0px;
  color: #666666;
  line-height: 1.2;
  font-size: 1em;
  font-weight: normal;
}

a {
  color: #34495E;
  text-decoration: underline;
}

a:hover {
  text-decoration: underline;
}

a.icon-link {
  text-decoration: none;
}

a.icon-link:hover {
  text-decoration: none;
}

main.main {
  display: block;
  position: relative;
  width: 95%;
  padding: 1em;
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
  max-width: 800px;
  padding-bottom: 60px;
}

main.main header h1.page-title {
  border-bottom: 2px solid #333333;
  line-height: 1;
  margin-bottom: 8px;
  margin-top: 1.2em;
  font-size: 28px;
}

@media only screen and (min-width: 640px) {
  main.main header h1.page-title {
    font-size: 36px;
  }
}

main.main header img.headshot {
  width: 80px;
  top: 2em;
  filter: grayscale(100%);
  border-radius: 50%;
  position: absolute;
  right: .5em;
}

@media only screen and (min-width: 640px) {
  main.main header img.headshot {
    width: 140px;
    top: 1em;
  }
}

table {
  margin: 1em 0;
  min-width: 300px;
  width: 100%;
}

table th {
  width: 33.3%;
}

footer {
  text-align: center;
  font-size: .9em;
  margin-top: 2em;
  border-top: 1px solid #333333;
  padding-top: 1em;
}

footer small {
  font-size: 1em;
}

table tr {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}

table td {
  display: block;
}

svg#email-icon:hover {
  fill: #0077B5;
}

svg#linkedin-icon:hover {
  fill: #0077B5;
}

table td:first-child {
  padding-top: .25em;
}

table td:last-child {
  padding-bottom: .25em;
}

address {
  font-style: normal;
}

.social-links {
  display: block;
  margin-top: .5em;
}

.hobby-images {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 400px;
}

.hobby-images img {
  max-width: 195px;
}

table th,
table td {
  text-align: left;
}

@media (min-width: 480px) {
  table th,
  table td {
    display: table-cell;
    padding: .15em .5em;
  }
  table th:first-child,
  table td:first-child {
    padding-left: 0;
  }
  table th:last-child,
  table td:last-child {
    padding-right: 0;
  }
}

table {
  background: #34495E;
  color: #fff;
  overflow: hidden;
}

table tr {
  border-color: #46637f;
}

table th,
table td {
  margin: .25em 1em;
}

@media (min-width: 480px) {
  table th,
  table td {
    padding: .25em !important;
  }
}

thead {
  padding: 0px;
}

thead > tr {
  border-bottom: 2px solid white;
  border-color: white;
  padding: 0px;
}

thead > tr th {
  padding: 0px;
}
